/* axiosInstance.js : Axios Ïù∏Ïä§ÌÑ¥Ïä§ Íµ¨ÏÑ± ÌååÏùº */
import axios from 'axios';

const instance = axios.create({
  baseURL: '/api', // baseURLÏùÑ ÏßÄÏ†ïÌïòÎ©¥ Ìò∏Ï∂úÌï† Îïå /api ÏÉùÎûµ Í∞ÄÎä•
  timeout: 5000
});

// Ïòà: ÏöîÏ≤≠ Ïù∏ÌÑ∞ÏÖâÌÑ∞, ÏùëÎãµ Ïù∏ÌÑ∞ÏÖâÌÑ∞ Ï∂îÍ∞Ä
instance.interceptors.request.use(config => {
  // Ïòà: ÌÜ†ÌÅ∞ ÏûêÎèô Ï∂îÍ∞Ä
  // config.headers.Authorization = `Bearer ${yourToken}`;
  return config;
}, error => Promise.reject(error));

instance.interceptors.response.use(
  res => res,
  err => {
    // Í≥µÌÜµ ÏóêÎü¨ Ï≤òÎ¶¨
    console.error(err);
    return Promise.reject(err);
  }
);

export default instance;