<template>
  <div class="form-wrapper">
    <h2>{{ isEditMode ? '‚úèÔ∏è Í≤åÏãúÍ∏Ä ÏàòÏ†ï' : 'üìù ÏÉà Í≤åÏãúÍ∏Ä ÏûëÏÑ±' }}</h2>
    <!-- form Ï†úÏ∂ú Ïãú handleSubmit Ìò∏Ï∂ú -->
    <form @submit.prevent="handleSubmit">
      <!-- ÏñëÎ∞©Ìñ• Î∞îÏù∏Îî©ÏúºÎ°ú ÏûÖÎ†•Í∞í Í¥ÄÎ¶¨ -->
      <input v-model="title" placeholder="Ï†úÎ™©" required />
      <textarea v-model="content" placeholder="ÎÇ¥Ïö©" required></textarea>
      <input v-model="writer" placeholder="ÏûëÏÑ±Ïûê" required />
      <div class="btn-group">
        <button type="submit">{{ isEditMode ? 'ÏàòÏ†ïÌïòÍ∏∞' : 'Îì±Î°ùÌïòÍ∏∞' }}</button>
        <!-- ÏàòÏ†ï Î™®ÎìúÏùº Í≤ΩÏö∞Îßå Ï∑®ÏÜå Î≤ÑÌäº ÌëúÏãú -->
        <button v-if="isEditMode" type="button" @click="resetForm" class="cancel">Ï∑®ÏÜå</button>
      </div>
    </form>
  </div>
</template>

<script>
import axios from 'axios';
export default {
  props: ['editingPost'], // ÏÉÅÏúÑÏóêÏÑú ÏàòÏ†ïÌï† Í≤åÏãúÍ∏Ä Îç∞Ïù¥ÌÑ∞ Ï†ÑÎã¨
  data() {
    return { id: null, title: '', content: '', writer: '' };
  },
  watch: {
    // editingPost Í∞íÏù¥ Î∞îÎÄî Îïå Ìï¥Îãπ Îç∞Ïù¥ÌÑ∞Î•º ÌèºÏóê Î∞òÏòÅ
    editingPost(newVal) {
      if (newVal) {
        this.id = newVal.id;
        this.title = newVal.title;
        this.content = newVal.content;
        this.writer = newVal.writer;
      }
    }
  },
  computed: {
    // ÏàòÏ†ï Î™®ÎìúÏù∏ÏßÄ Ïó¨Î∂ÄÎ•º ÌåêÎã®
    isEditMode() {
      return this.id !== null;
    }
  },
  methods: {
    handleSubmit() {
      const payload = { title: this.title, content: this.content, writer: this.writer };
      // ÏàòÏ†ï Î™®Îìú: PUT ÏöîÏ≤≠ / ÏÉà ÏûëÏÑ±: POST ÏöîÏ≤≠
      const req = this.isEditMode
        ? axios.put(`/api/boards/${this.id}`, payload)
        : axios.post('/api/boards', payload);

      // ÏÑ±Í≥µ ÌõÑ Ïù¥Î≤§Ìä∏ Î∞úÏÉù Î∞è Ìèº Ï¥àÍ∏∞Ìôî
      req.then(() => {
        this.$emit('saved');  // Î∂ÄÎ™®ÏóêÍ≤å Ï†ÄÏû• ÏôÑÎ£å ÏïåÎ¶º
        this.resetForm();
      });
    },
    resetForm() {
      // Ìèº ÏûÖÎ†• Ï¥àÍ∏∞Ìôî
      this.id = null;
      this.title = '';
      this.content = '';
      this.writer = '';
    }
  }
}
</script>

