.form-wrapper {
  background: #fff;
  padding: 20px;
  border-radius: 12px;
  margin-bottom: 20px;
}
input, textarea {
  width: 100%;
  padding: 12px;
  margin-bottom: 10px;
  border: 1px solid #ccc;
  border-radius: 8px;
}
